}
.main-list__box-desc {
  margin-top: 15px;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .main-list {
    margin: 63px -30px 60px;
    font-size: 0;
    display: flex;
    flex-wrap: wrap;
  }
  .main-list__item {
    display: flex;
    width: 50%;
    padding: 0 30px;
    margin-bottom: 60px;
  }
  .main-list__box {
    height: 100%;
  }
}
@media (min-width: 991px) {
  .main-list {
    margin-top: 60px;
  }
  .main-list__item {
    width: 33.333%;
  }
}
@media (min-width: 1200px) {
  .main-list {
    margin-bottom: -7px;
  }
}

.slider {
  margin-top: 50px;
  font-size: 0;
  /* Slider */
}
.slider .slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slider .slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slider .slick-list:focus {
  outline: none;
}
.slider .slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slider .slick-slider .slick-track,
.slider .slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slider .slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slider .slick-track:before, .slider .slick-track:after {
  content: "";
  display: table;
}
.slider .slick-track:after {
  clear: both;
}
.slick-loading .slider .slick-track {
  visibility: hidden;
}
.slider .slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
}
[dir=rtl] .slider .slick-slide {
  float: right;
}
.slider .slick-slide img {
  display: block;
}
.slider .slick-slide.slick-loading img {
  display: none;
}
.slider .slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slider .slick-slide {
  display: block;
}
.slick-loading .slider .slick-slide {
  visibility: hidden;
}
.slick-vertical .slider .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slider .slick-arrow.slick-hidden {
  display: none;
}
.slider .slick-dots {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.slider .slick-dots button {
  background: #24A3FF;
  opacity: 0.2;
  border-radius: 50%;
  content: "";
  cursor: pointer;
  border: none;
  width: 16px;
  height: 16px;
  margin-right: 7px;
  margin-left: 7px;
  color: transparent;
  transition: ease 0.4s;
}
.slider .slick-dots button:hover {
  opacity: 0.5;
}
.slider .slick-active button {
  opacity: unset;
}
.slider__box {
  border-radius: 12px;
  background: #F5F5FA;
  display: flex;
  flex-direction: column;
}
.slider__box-img {
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  object-fit: cover;
  width: 100%;
  height: 500px;
}
.slider__box-text {
  padding: 0 30px;
  margin-top: 30px;
}
.slider__box-link {
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #24A3FF;
  transition: ease 0.5s;
}
.slider__box-link:hover {
  color: #F0145A;
  text-decoration: underline;
}
.slider__box-link:hover path {
  stroke: #F0145A;
}
.slider__box-link svg {
  transition: ease 0.5s;
}
.slider__box-link:hover svg {
  transform: translateX(10px);
}
.slider__box-headline {
  margin-top: 5px;
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
  word-wrap: break-word;
  background: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
